<template>
  <v-layout row wrap v-resize="onResize">
    <v-flex xs12>
      <v-card hover class="ma-2 pa-2">
        <v-layout row wrap>
          <v-flex xs2>
            <v-radio-group v-model="weatherDataOption" @change="weatherDataChange()">
              <v-radio label="Temp. High (deg F)" :value="0"></v-radio>
              <v-radio label="Precip. (Inches)" :value="1"></v-radio>
              <v-radio label="Wind (Mph)" :value="2"></v-radio>
            </v-radio-group>
          </v-flex>
          <v-flex xs10>
            <div ref='dataZoom' class='mt-0 pt-0' style='height:150px;width:100%'></div>
          </v-flex>
        </v-layout>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
  import shine from '@/components/plugins/echarts-theme-macaroons'
  import weatherData from '@/assets/weatherData.json';

  export default{
    data: () => ({
      dataZoom: null,
      weatherDataOption: 0,
    }),
    methods: {
      onResize() {
        if(this.dataZoom)_.debounce(this.dataZoom.resize,100)()
      },
      changeDataZoom(){
        this.$store.commit('changeDates',this.dataZoom.getOption());
      },
      weatherDataChange(){
        this.refreshDataZoom;
      }
    },
    computed: {
      getWeatherData: function() {
        return (weatherData)
      },
      filterData(){
        // let startIndex = this.dataZoom.getOption().dataZoom[0].startValue;
        // let endIndex = this.dataZoom.getOption().dataZoom[0].endValue;
        let filteredData = this.getWeatherData.filter(data => new Date(data.Time) >= new Date(this.dateData[this.$store.state.startDate])  && new Date(data.Time) <= new Date(this.dateData[this.$store.state.endDate]));
        return filteredData;
      },
      dateData(){
        let dateData = [...new Set(this.getWeatherData.map(({Time}) => new Date(Time).toLocaleDateString()))];
        if(dateData[dateData.length-1]=='12/31/1969'){
          dateData.pop();
        }
        return dateData;
      },
      dayMonthData(){
        let dates = [...this.dateData];
        dates.forEach((item,ind,arr)=>arr[ind]=(item.substring(0,item.length-5)));
        return dates;
      },
      tempData(){
        let tempHighs = [];
        let tempAvg = [];
        let precip = [];
        let windspeed = [];
        for(const date in this.dateData){
          let oneDayData = (this.getWeatherData.filter(data => new Date(data.Time).toLocaleDateString() == this.dateData[date]));
          //tempHigh.push(Math.max(...oneDayData.map(data => data.DBTdgF)));
          tempAvg.push(oneDayData.reduce((total,arr) => total+arr.DBTdgF,0)/oneDayData.length); //average Temp
          precip.push(oneDayData.reduce((total,arr) => total+arr.Precipin,0)); //cumulative rain in day
          windspeed.push(oneDayData.reduce((total,arr) => total+arr.WindSpeedmph,0)/oneDayData.length); //average windspeed
        }
        // console.log(precip)
        return [tempAvg,precip,windspeed];
        //return this.filterData.map(data => data.DBTdgF);
      },
      refreshDataZoom: function(){
        this.dataZoom.showLoading()
        // console.log(...this.dateData.forEach(this.dayMonthData))
        let yAxisLabel = ['Degrees F','Inches','MPH'];
        // let length = [];
        // length.length = this.dateData.length;
        let dataZoomOption = {
          title: {
            text: 'Environmental Conditions',
            left: 'center',
          },
          grid: {
            bottom: 20,
          },
          xAxis: {
            type: 'category',
            data: this.dayMonthData,
            axisLabel: {
              show: false,
            },
          },
          yAxis: {
            type: 'value',
            show: true,
            scale: true,
            name: yAxisLabel[this.weatherDataOption],
            nameRotate: 90,
            nameLocation: 'center',
            nameGap: 30,
          },
          dataZoom: [
            {
              name: 'dataZoom',
              type: 'slider',
              show: true,
              start: 0,
              end: 100,
              handleSize: 30,
              filterMode: 'filter',
              textStyle: {
                fontWeight: 'bold',
                fontSize: 16,
              }
            },
          ],
          series: [
            {
              name: 'dataZoom',
              type: 'line',
              data: this.tempData[this.weatherDataOption],
            }
          ]
        };
        dataZoomOption.dataZoom[0].startValue = this.$store.state.startDate;
        dataZoomOption.dataZoom[0].endValue = this.$store.state.endDate;
        this.dataZoom.setOption(dataZoomOption,true);
        this.dataZoom.hideLoading();
      },
    },
    mounted: function () {
      this.tempData;
      echarts.registerTheme('shine', shine)
      this.dataZoom = echarts.init(this.$refs.dataZoom,'shine');
      this.refreshDataZoom;
      this.dataZoom.on('dataZoom',this.changeDataZoom);
      this.onResize();
    }

  }
</script>
